<template>
  <div class="commentBox">
    <div class="title" id="title">
      <h4>{{commentList.totalCount}}</h4>
    </div>
    <div class="commentContent">
      <ul>
        <li v-for="(item,index) in commentList.scCmsCommentList" :key="index">
          <div class="commentBy">
            <div class="commentByName">
              <img :src="item.userImgUrl?item.userImgUrl:require('../../../static/images/mrtx.png')" alt="">
              <span>{{item.userName}}</span>
            </div>
            <div class="likeComment">
              <span class="iconfont icon-dianzanshu" @click="agree(item.id,item.isPraise)" :class="{'red':item.isPraise==1}">{{item.praiseNumber}}</span>
            </div>
          </div>
          <div class="content">
            <p>{{item.commentComment}}</p>
            <p><span>{{item.commentTime}}</span><span>{{item.commentReplyNum}}&nbsp;></span></p>
          </div>
        </li>
        <!--<li>-->
          <!--<div class="commentBy">-->
            <!--<div class="commentByName">-->
              <!--<img src="http://oss-cn-hangzhou.aliyuncs.com/tsnrhapp/user/photos/0e7e8be7966d84e421e20b1a161d04fe.png" alt="">-->
              <!--<span></span>-->
            <!--</div>-->
            <!--<div class="likeComment">-->
              <!--<span class="iconfont icon-dianzanshu">56</span>-->
            <!--</div>-->
          <!--</div>-->
          <!--<div class="content">-->
            <!--<p>3,</p>-->
            <!--<p><span>10-10&nbsp;14:48</span><span>110&nbsp;></span></p>-->
          <!--</div>-->
        <!--</li>-->
      </ul>
    </div>
  </div>
</template>

<script>
  import $ from 'jquery'
  import qs from 'qs'
  import {Toast} from 'mint-ui'
  export default {
    props:['userId','articleId'],
    data: function () {
      return {
        commentList:[],
        totalCommentNum:0
        //isEnshrined:0,
        //show: false,
        // commentContent:'',
      }
    },
    created:function () {
      this.getCommentList(this.articleId);
    },
    methods: {
      //
      getCommentList:function (articleId) {
        var url = '/zxcity_restful/ws/rest';
        var data = {
          cmd: 'cms_new/selectTimeNewCmsComment',
          data: JSON.stringify({
            "userId": sessionStorage.getItem('userId'),
            "pagination": {
              "page": 1,
              "rows": 10
            },
            "commentId": 0,
            "articleId": articleId
          }),
          version: 1
        };
        this.$axios.post(url,qs.stringify(data)).then((res)=>{
          if(res.data.code!=1){
            Toast(res.data.msg);
            return
          }
          this.commentList = res.data.data;
          this.totalCommentNum = res.data.data.totalCount
        })
      },
      // 
      agree:function (commentId,isPraise) {
        if(isPraise===1){
          Toast('');
          return;
        }
        if(!sessionStorage.getItem('userId')){
          this.$parent.$parent.showLoginPage?this.$parent.$parent.showLoginPage():this.$parent.showLoginPage();
          return
        }
        var url = '/zxcity_restful/ws/rest';
        var data = {
          cmd: 'cms_new/addPraiseToCommentNew',
          data: JSON.stringify({
            "userImgUrl": sessionStorage.getItem('userPic'),
            "userId": sessionStorage.getItem('userId')||'',
            "userName": sessionStorage.getItem('userName'),
            "commentId":commentId
          }),
          version: 1
        };
        this.$axios.post(url,qs.stringify(data)).then((res)=>{
          if(res.data.code!==1){
            Toast(res.data.msg);
            return
          }
          Toast(res.data.msg);
          this.getCommentList(this.articleId)
        })
      },
    },
    updated: function () {
      this.$nextTick(function () {
        $('.mask').height($(window).height())
      })
    },
  }
</script>

<style scoped lang="less" type="text/less">
  .commentBox{
    position: relative;
  }
  .title {
    width: 100%;
    height: 77/32rem;
    line-height: 77/32rem;
    font-size: 30/32rem;
    color: #787878;
    padding-left: 30/32rem;
    border-top: 1/32rem solid #eee;
    h4 {
      font-weight: 400;
    }
  }
  .commentContent {
    width: 100%;
    padding-bottom: 20/32rem;
    ul {
      background-color: #fff;
      li {
        padding: 0 30/32rem;
        border-top: 2/32rem solid #eee;
        .commentBy {
          width: 100%;
          height: 68/32rem;
          line-height: 68/32rem;
          margin-top: 18/32rem;
          .commentByName {
            float: left;
            img {
              width: 68/32rem;
              height: 68/32rem;
              border-radius: 50%;
              vertical-align: middle;
              display: inline-block;
            }
            span {
              font-size: 30/32rem;
              color: #9189f2;
            }
          }
          .likeComment {
            float: right;
            font-size: 30/32rem;
            color: #787878;
            span::before {
              font-size: 36/32rem;
            }
          }
        }
        .content {
          margin-top: 10/32rem;
          padding: 0 80/32rem;
          margin-bottom: 25/32rem;
          p:nth-of-type(1) {
            font-size: 30/32rem;
            color: #333333;
            line-height: 40/32rem;
            word-break: break-all;
            text-overflow: ellipsis;
            display: -webkit-box; /**  **/
            -webkit-box-orient: vertical; /**  **/
            -webkit-line-clamp: 2; /**  **/
            overflow: hidden; /**  **/
          }
          p:nth-of-type(2) {
            margin-top: 15/32rem;
            font-size: 24/32rem;
            padding: 10/32rem 0;
            color: #787878;
            span {
              margin-right: 30/32rem;
            }
          }
        }
      }
    }
  }
  .red{
    color: red;
  }
</style>



