<template>
  <div ref="wrapper" style="background-color: #fff">
    <div class="content-bg better-scroll-container">  <!---->
      <div> <!---->
        <div v-show="showTopTips" class="pulldown-tip" ref="showTips" v-if="showTopTip">
          <i class="indexicon" :class="[pulldownTip.rotate]"></i>
          <span class="tip-content">{{pulldownTip.text}}</span>
          <p>{{lastRefreshTime}}</p>
        </div>
        <div class="pullUp" v-if="refresh_ing"><span>...</span><p>:{{lastRefreshTime}}</p></div>
      </div>
      <slot></slot>
      <div class="bottomLoading" v-show="showBottomTips&&showBottomTips_1"><img :src="tipLoading" v-if="tipLoading"><span>{{bottomTips}}</span></div>
    </div>
  </div>
</template>
<script>
  import BScroll from 'better-scroll'
  import $ from 'jquery'
  export default {
    props: {
      /**
       * 1 scroll
       * 2 scroll
       * 3 scrollswipescroll
       */
      probeType: {
        type: Number,
        default:1
      },
      /**
       * click
       */
      click: {
        type: Boolean,
        default: true
      },
      /**
       * 
       */
      scrollX: {
        type: Boolean,
        default: false
      },
      scrollY: {
        type: Boolean,
        default: false
      },
      /**
       * 
       */
      listenScroll: {
        type: Boolean,
        default: true
      },
      /**
       * 
       */
      data: {
        type: Array,
        default: null
      },
      /**
       * 
       */
      pullup: {
        type: Boolean,
        default: false
      },
      /**
       * 
       */
      pulldown: {
        type: Boolean,
        default: false
      },
      /**
       * 
       */
      beforeScroll: {
        type: Boolean,
        default: true
      },
      /**
       * scroll
       */
      refreshDelay: {
        type: Number,
        default: 20
      },
      /**
       * 
       */
      lastRefreshTime:{
        type:String,
        default:''
      },
      directionLockThreshold: {
        type: Number,
        default: 5
      },
      //
      //loadMoreLength:'',
      //
      refreshDataLength:'',
      //
      showBottomTips:{
        type: Boolean,
        default: true
      },
      rows: {
        type: Number,
        default: 10
      },
      totalNum:'',
      showTopTip:{
        type: Boolean,
        default: true
      }//
    },
    data() {
      return {
        loadingConnecting: false,
        pulldownTip: {
          text: '',     // 
          rotate: 'iconfont icon-shangla-copy'    // icon-rotate
        },
        tipLoading:require('../../../static/images/Spinner.gif'),//
        bottomTips:'...',//
        refresh_ing:false,
        showTopTips:true,
        // showBottomTips_1:false
      };
    },
    mounted() {
      // DOMbetter-scroll
      setTimeout(() => {
        this._initScroll()
      }, 20)

    },
    methods: {
      _initScroll() {
        if (!this.$refs.wrapper) {
          return
        }
        // better-scroll
        this.scroll = new BScroll(this.$refs.wrapper, {
          probeType: this.probeType,
          click: this.click,
          scrollX: this.scrollX,
          // pullDownRefresh:{threshold:30,stop:30},
          // pullUpLoad:{threshold:0,txt:{more: '', noMore: ''}}
        });

        // 
        if (this.listenScroll || this.pulldown || this.pullup) {
          let me = this;
          this.scroll.on('scroll', (pos) => {
            if (this.listenScroll) {
              me.$emit('scroll', pos);
            }

            if (this.pulldown) {
              // 
              if (pos.y > 50) {
                this.pulldownTip = {
                  text: '',
                  rotate: 'iconfont icon-shangla'
                }
              } else if(pos.y >5){
                this.showTopTips = true;
                this.pulldownTip = {
                  text: '',     // 
                  rotate: 'iconfont icon-shangla-copy'    // icon-rotate
                }
              }
            }
            if (this.pullup) {
              // console.log(this.scroll.maxScrollY);
              // console.log(pos.y);
            }
          })
        }

        // 
        if (this.pullup) {
          this.scroll.on('scrollEnd', () => {
            // 
            if (this.scroll.y <= (this.scroll.maxScrollY + 150)&&this.scroll.y!==0) {
              this.$emit('scrollToEnd');
            }
          })
        }

        // 
        if (this.pulldown) {
          this.scroll.on('touchEnd', (pos) => {
            // 
            if (pos.y > 50) {
              setTimeout(() => {
                // 
                this.pulldownTip = {
                  text: '',     // 
                  rotate: 'iconfont icon-shangla-copy'  // icon-rotate
                }
              },600);
              this.$emit('pulldown');
              this.showTopTips = false;
              this.refresh_ing = true;
              this.tipLoading = require('../../../static/images/Spinner.gif');
              this.bottomTips = '...';
            }else{
              this.showTopTips = false;
            }
          });
        }

        // 
        if (this.beforeScroll) {
          this.scroll.on('beforeScrollStart', () => {
            this.$emit('beforeScroll')
          })
        }

        if (this.listenScroll) {
          let me = this;
          this.scroll.on('scroll', (pos) => {
            me.$emit('scroll', pos)
          })
        }
      },
      disable() {
        // better-scrolldisable
        this.scroll && this.scroll.disable()
      },
      enable() {
        // better-scrollenable
        this.scroll && this.scroll.enable()
      },
      refresh() {
        // better-scrollrefresh
        this.scroll && this.scroll.refresh()
      },
      scrollTo() {
        // better-scrollscrollTo
        this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
      },
      scrollToElement() {
        // better-scrollscrollToElement
        this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
      }
    },
    created:function () {
      this.tipLoading = require('../../../static/images/Spinner.gif');
      this.bottomTips = '...';
    },
    watch: {
      // refreshDelayrefresh
      data(val,old) {
        setTimeout(() => {
          this.refresh();
          setTimeout(()=>{
            this.refresh_ing = false
          },500)
        }, this.refreshDelay);
        // 
        // if(val.length===old.length&&val.length!==this.rows){
        //   this.bottomTips = '(*)';
        //   this.tipLoading = '';
        // }else{
        //   this.tipLoading = require('../../../static/images/Spinner.gif');
        //   this.bottomTips = '...';
        // }
        // console.log(val.length,this.totalNum);
        if(val.length===this.totalNum&&val.length>0&&this.totalNum>0){
          this.bottomTips = '(*)';
          this.tipLoading = '';
        }
      },
      // loadMoreLength:function (val, old) {
      //   if(val==0){
      //     // this.bottomTips = '(*)';
      //     // this.tipLoading = '';
      //   }
      // },
      refreshDataLength:function (val, old) {
        if(val<this.rows){
          this.bottomTips = '';
          this.tipLoading = '';
          return
        }
        this.tipLoading = require('../../../static/images/Spinner.gif');
        this.bottomTips = '...';
      }
    },
    computed:{
      showBottomTips_1:function () {
        if((this.refreshDataLength<this.rows)&&(this.refreshDataLength!=' ')){
          return false
        }else{
          return true
        }
      }
    }
  }
</script>
<style scoped type="text/less" lang="less">
  @cube-size: 10/32rem;
  .better-scroll-root {
    background-color: rgba(7, 17, 27, 0.7);
    .loading-pos, .pulldown-tip {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 35/32rem;
      color: #fcfcfc;
      text-align: center;
      z-index: 2000;
    }
    .loading-pos {
      background-color: rgba(7, 17, 27, 0.7);
    }
    .pulldown-tip {
      top: -50/32rem;
      height: 50/32rem;
      line-height: 50/32rem;
      z-index: 1;
    }
    .pull-icon {
      position: absolute;
      top: 0;
      left: 30%;
      color: #a5a1a1;
      font-size: 1.5em;
      transition: all 0.15s ease-in-out;
    }
    .pull-icon.icon-rotate {
      transform:rotate(180deg);
    }

    /*<!--.loading-container {-->*/
      /*<!--position: absolute;-->*/
      /*<!--height: @cube-size;-->*/
      /*<!--width: @cube-size;-->*/
      /*<!--left: 35%;-->*/
      /*<!--top: 50%;-->*/
      /*<!--transform: translate(-50%, -50%);-->*/
      /*<!--perspective: 40/32rem;-->*/
    /*<!--}-->*/
    /*<!--.loading-connecting {-->*/
      /*<!--line-height: 35/32rem;-->*/
    /*<!--}-->*/
    @keyframes rotate{
      0%{
        transform:rotateX(0deg) rotateY(0deg);
      }
      50%{
        transform:rotateX(360deg) rotateY(0deg);
      }
      100%{
        transform:rotateX(360deg) rotateY(360deg);
      }
    }
  }
  .pulldown-tip{
    width: 100%;
    position: absolute;
    top: -104/32rem;
    left: 0;
    text-align: center;
    padding: 10/32rem 0;
    line-height: 36/32rem;
    font-size: 24/32rem;
    /*animation: scale 0.3s;*/
  }
  .bottomLoading{
    width: 100%;
    height: 80/32rem;
    line-height: 80/32rem;
    text-align: center;
    color: #666666;
    font-size: 26/32rem;
    position: absolute;
    bottom: -80/32rem;
    left: 0;
    img{
      display: inline-block;
      width: 80/32rem;
      vertical-align: middle;
    }
  }
  .pullUp{
    text-align: center;
    font-size: 24/32rem;
    line-height: 36/32rem;
    padding: 10/32rem 0;
    background-color: transparent;
  }
  @keyframes scale {
    from{
      height: 0/32rem;
      transform: scale(0);
    }
    to{
      height: 90/32rem;
      transform: scale(1);
    }
  }
</style>



